import React from 'react';
import { Navbar } from 'react-bootstrap';
//import { render } from '@testing-library/react';
import MetaTags from 'react-meta-tags';
import { isMobile, isTablet } from 'react-device-detect';

import './App.css';

import ContentCard from './ContentCard';
import LandPriceCard from './LandPriceCard';

import { DataModel, formatDate } from './DataSource';

import YahooFinanceCard from './YahooFinanceCard';
import ComparisonCard from './ComparisonCard';

function App() {
  let dow_jones_index_card;
  let kospi_index_card;
  let exchange_rate_card;
  let land_price_card;
  let gold_index_card;

  if (DataModel.shared().isProduction()) {
    if (DataModel.shared().isLandShown()) {
      land_price_card = <LandPriceCard title='서울 부동산 매매가격지수' text='서울 부동산 매매 가격의 등락을 시각화한다.' isPeriodShown='false'></LandPriceCard>
    }

    dow_jones_index_card = <YahooFinanceCard code='^DJI' title='다우존스 지수' text='다우존스 지수를 시각화한다.'></YahooFinanceCard>
    kospi_index_card = <YahooFinanceCard code='^KS11' title='코스피 지수' text='코스피 지수를 시각화한다.'></YahooFinanceCard>
    exchange_rate_card = <YahooFinanceCard code='KRW=X' title='환율' text='원/달러 환율'></YahooFinanceCard>
    gold_index_card = <YahooFinanceCard code='GC=F' title='금 지수' text='금 지수를 시각화한다.'></YahooFinanceCard>
  } else {
    kospi_index_card = <YahooFinanceCard code='^KS11' title='코스피 지수' text='코스피 지수를 시각화한다.'></YahooFinanceCard>
  }

  let today = formatDate(Date.now() / 1000);
  var app_title = <h3>{today}</h3>;
  if (isTablet) ;
  else if (isMobile) app_title = <h6>{today}</h6>;

  var class_app_header = 'App-header';
  if (isTablet) ;
  else if (isMobile) class_app_header = 'Mobile-App-header';

  return (
    <div className="App">
      <MetaTags>
        <meta name='viewport' content='width=device-width, user-scalable=no'/>
        <meta name='theme-color' content='#f2f2f2'/>
      </MetaTags>
      <header className={class_app_header}>
        <Navbar expand='lg' style={{ padding:'0.5rem 2.5%' }}>
          <Navbar.Brand href="#">{app_title}</Navbar.Brand>
        </Navbar>
        { land_price_card }
        { kospi_index_card }
        { dow_jones_index_card }
        { exchange_rate_card }
        { gold_index_card }
        <ComparisonCard code='^KS11' title='코스피와 환율' text='코스피와 환율'></ComparisonCard>
        <ContentCard title='경제 지표의 상관관계 (준비중)' text='경제 지표를 상관관계지수를 통해 시각화한다.'></ContentCard>
        <ContentCard title='주식과 부동산 (준비중)' text='주식과 부동산 어느 지표가 더 저평가 되어 있나?'></ContentCard>
      </header>
    </div>
  );
}

export default App;
